import _ from "lodash";
import { computed } from "mobx";

export const OverviewModules = {
    AutoInvestBanner: "AutoInvestBanner",
    DepositBannersList: "DepositBannersList",
    RecommendedPlanBanner: "RecommendedPlanBanner",
    PersonalSnapshot: "PersonalSnapshot",
    AccountValueGraph: "AccountValueGraph",
    ReturnsBox: "ReturnsBox",
    RewardsBox: "RewardsBox",
    HistoricalReturnsCarousel: "HistoricalReturnsCarousel",
    ReferralModule: "ReferralModule",
    QualityValueProps: "QualityValueProps",
    DepositInProgressTracker: "DepositInProgressTracker",
    PortfolioPie: "PortfolioPie",
    ProjectedReturnGraph: "ProjectedReturnGraph",
    DiversificationGraph: "DiversificationGraph",
    AutoInvestModule: "AutoInvestModule",
    BookACall: "BookACall",
    RegionDiversityGraph: "RegionDiversityGraph",
    WhiskeyVestBanner: "WhiskeyVestBanner",
    // ReferralPromotion: "ReferralPromotion",
};

export class SkeletonStore {
    static build(rootStore) {
        return new SkeletonStore(rootStore);
    }

    constructor(rootStore) {
        this.root = rootStore;
    }

    @computed get getOverviewModules() {
        const rs = this.root;

        let banner = OverviewModules.RecommendedPlanBanner;

        if (rs.transfer.skeletonAccountSubscription && rs.portfolio.totalAccountValue) {
            banner = OverviewModules.AutoInvestBanner;
        }

        if (rs.transfer.pendingDeposits.length || rs.transfer.sourcingWines.length) {
            banner = OverviewModules.DepositBannersList;
        }

        let base = [
            {
                type: banner,
            },
            { type: OverviewModules.PersonalSnapshot },
            { type: OverviewModules.AccountValueGraph },
            { type: OverviewModules.RewardsBox },
            { type: OverviewModules.ReturnsBox },
            { type: OverviewModules.RegionDiversityGraph },
            { type: OverviewModules.HistoricalReturnsCarousel },
            // { type: OverviewModules.ReferralPromotion },
            { type: OverviewModules.BookACall },
            { type: OverviewModules.ReferralModule },
            { type: OverviewModules.WhiskeyVestBanner },
            { type: OverviewModules.QualityValueProps },
        ];

        if (rs.transfer.hasMadeTransfers) {
            base = _.filter(base, (m) => m.type !== OverviewModules.HistoricalReturnsCarousel);
        }

        if (rs.portfolio.accountLevel === "Standard" && rs.portfolio.totalAccountValue > 0) {
            base = _.filter(base, (m) => m.type !== OverviewModules.BookACall);
        }

        return base;
    }
}
