import React from "react";
import styled from "styled-components";

export const WireTable = ({ data }) => (
    <ContentContainer>
        {data.map(({ title, value, isReference }) => (
            <React.Fragment key={title}>
                <Title>{title}</Title>
                <Text>
                    {isReference ? (
                        <Reference>{value}</Reference>
                    ) : (
                        value.map((val) => (
                            <React.Fragment key={val}>
                                {val}
                                <br />
                            </React.Fragment>
                        ))
                    )}
                </Text>
            </React.Fragment>
        ))}
    </ContentContainer>
);

export const ContentContainer = styled.div`
    display: grid;
    grid-template-columns: 42fr 58fr;
    gap: 0px 0px;
    grid-template-areas: ". .";
    margin: 0 auto;

    ${(p) => p.theme.media.greaterThan("768px")`
        max-width: 666px;
        grid-template-columns: 1fr 1fr;
    `}
`;

export const TextGeneral = styled.div`
    font-family: VinovestMedium;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.005em;
    color: #5b646b;
    border-bottom: 1px solid #caccce;
    padding: 19px 0;
    height: 100%;
    display: flex;
    align-items: center;

    ${(p) => p.theme.media.greaterThan("768px")`
        font-size: 16px;
        line-height: 26px;
    `}
`;

export const Title = styled(TextGeneral)`
    padding-right: 16px;
`;

export const Text = styled(TextGeneral)`
    color: #242e35;
    font-size: 16px;
    line-height: 26px;

    ${(p) => p.theme.media.greaterThan("768px")`
        font-size: 20px;
        line-height: 36px;
    `}
`;

const Reference = styled.div`
    background: #fae8d1;
    padding: 0 14px 0 7px;
`;
