    getLwin7ContentfulData = (keys) => {
        const lwin7List = Array.from(new Set(keys.map((key) => key.slice(0, 7))));

        return this.contentfulClient.getEntries({
            content_type: "lwin7",
            "fields.lwin7[in]": lwin7List.join(","),
        });
    };

    getLwin7Descriptions = async (lwin7Arr) => {
        const fetchLwin7 = await this.getLwin7ContentfulData(lwin7Arr);

        let lwin7Acc = {};

        try {
            lwin7Acc = fetchLwin7.items.reduce(
                (acc, { fields: { displayName, description, lwin7 } }) => ({
                    ...acc,
                    [lwin7]: displayName || description,
                }),
                {},
            );
        } catch (err) {
            lwin7Acc = {};
        }

        return lwin7Acc;
    };

    getWinesFromContentful = async (keys) => {
        let data;
        const lwin11 = keys;

        const fetchLwin11 = this.contentfulClient.getEntries({
            content_type: "vintage",
            "fields.lwin11[in]": lwin11.join(","),
        });

        const fetchLwin7 = this.getLwin7ContentfulData(keys);

        const fetchCritic = this.contentfulClient.getEntries({
            content_type: "critic",
            "fields.lwin11[in]": lwin11.join(","),
        });

        try {
            const response = await Promise.all([fetchLwin11, fetchLwin7, fetchCritic]);

            const lwin11Keys = response[0].items.reduce(
                (acc, { fields }) => ({ ...acc, [fields.lwin11]: { ...fields } }),
                {},
            );

            const lwin7Keys = response[1].items.reduce(
                (acc, { fields }) => ({ ...acc, [fields.lwin7]: { ...fields } }),
                {},
            );

            const criticKeys = response[2].items.reduce(
                (acc, { fields }) => ({
                    ...acc,
                    [fields.lwin11]: acc[fields.lwin11] ? [...acc[fields.lwin11], fields] : [fields],
                }),
                {},
            );

            const allKeys = keys.reduce(
                (acc, key) => ({
                    ...acc,
                    [key]: {
                        ...lwin7Keys[key.slice(0, 7)],
                        ...lwin11Keys[key],
                        critics: criticKeys[key],
                    },
                }),
                {},
            );

            data = { ok: true, data: allKeys };
        } catch (err) {
            data = { ok: false };
        }

        return data;
    };

