import React from "react";
import { V3Logo } from "#assets/shared/v3Logo";
import { DesktopNavBar } from "./DesktopNavBar";
import { NavLogo, StyledNav, FirstNavSection, NavAnimationWrapper, NavAnimationContainer } from "../Navigation.styled";
import { ConditionalLink } from "./ConditionaLink";

const DesktopNavWrapper = ({
    classNameHeader,
    isOnboarding,
    logoType,
    isSticky,
    height,
    isAddFunds,
    authenticated,
    pagesWithAnimation,
}) => {
    if (!pagesWithAnimation) {
        return (
            <NavBar
                {...{
                    isSticky: true,
                    isOnboarding,
                    logoType,
                    authenticated,
                    classNameHeader: `darkBlue darkBlue__constant ${isSticky ? "withConstantShadow" : ""}`,
                    height,
                    isAddFunds,
                }}
            />
        );
    }

    return (
        <NavAnimationWrapper className={isSticky ? "sticky" : ""}>
            <NavAnimationContainer className={`outFromHtml ${!isSticky ? "openNavBar" : "closeBavBar"}`}>
                <NavBar
                    {...{ isSticky: false, isOnboarding, logoType, authenticated, classNameHeader, height, isAddFunds }}
                />
            </NavAnimationContainer>

            <NavAnimationContainer className={`${isSticky ? "openNavBar" : "closeBavBar"}`}>
                <NavBar
                    {...{
                        isSticky: true,
                        isOnboarding,
                        logoType: "standard",
                        authenticated,
                        classNameHeader: "darkBlue darkBlue__constant sticky",
                        height,
                        isAddFunds,
                    }}
                />
            </NavAnimationContainer>
        </NavAnimationWrapper>
    );
};
const NavBar = ({ isSticky, isOnboarding, logoType, authenticated, classNameHeader, height, isAddFunds }) => (
    <StyledNav isSticky={isSticky} height={height} className={`${classNameHeader} ${isAddFunds ? "withBorder" : ""}`}>
        <FirstNavSection>
            <NavLogo isCompact={isSticky}>
                <ConditionalLink condition={!isOnboarding}>
                    <V3Logo isCompact={isSticky} type={logoType} />
                </ConditionalLink>
            </NavLogo>
        </FirstNavSection>
        {!isAddFunds && (
            <DesktopNavBar authenticated={authenticated} linkClassName={classNameHeader} isSticky={isSticky} />
        )}
    </StyledNav>
);

export default DesktopNavWrapper;
