import * as React from "react";
import { useRouteMatch } from "react-router-dom";
import ServiceBellWidget from "@servicebell/widget";
import { ROUTE_PATHS } from "#screens/route-paths";
import { useTimedCallback } from "#shared/hooks/useTimedCallback";
import { useRootStore } from "#shared/hooks/useRootStore";
import { getServiceBellHours } from "#app.config";

interface IServiceBellProps {
    children: JSX.Element;
    disabled?: boolean;
}
function ServiceBell({ children, disabled }: IServiceBellProps) {
    const { startDate: startTime, endDate: endTime } = getServiceBellHours();
    const { user } = useRootStore();

    const serviceBellMatch = useRouteMatch({
        path: [ROUTE_PATHS.liquidation, ROUTE_PATHS.beforeYouRequest], // Paths to show service bell
        strict: true,
        sensitive: true,
    });

    const serviceBellEffectCallback = useTimedCallback(
        () => {
            if (serviceBellMatch?.path && !disabled) {
                ServiceBellWidget.alert({ ...user?.oktaUserInfo });
                return ServiceBellWidget.show();
            }
            return ServiceBellWidget.hide();
        },
        [serviceBellMatch],
        { startTime, endTime },
    );

    React.useEffect(serviceBellEffectCallback, [serviceBellMatch]);
    return <>{children}</>;
}

export default ServiceBell;
