import React from "react";
import { useLocation } from "react-router-dom";
import { getData } from "#models/FetchStatus";

const optimizePhoneABKey = "fUYARp2LTwqfdf7DVVe9Zw";
const originVariant = "0";

const CheckPhoneNumberABVariant = ({ setVariant, oktaUserEntity }) => {
    const location = useLocation();

    const onPersonalize = location.pathname.includes("personalize");

    const localStorageKey = React.useMemo(() => {
        const { email } = getData(oktaUserEntity);

        return `${email}_storePhoneNumberABVariant`;
    }, [oktaUserEntity]);

    React.useEffect(() => {
        let intervalClosure;
        let waitCounter = 0;

        // use last variant if user is already on quiz
        if (onPersonalize) {
            setVariant(window.localStorage.getItem(localStorageKey) || originVariant);
        } else if (window.google_optimize !== undefined && !window.google_optimize.get(optimizePhoneABKey)) {
            // check for google optimize variants

            const setOptimize = async () => {
                if (window.dataLayer) {
                    await window.dataLayer.push({ event: "CheckPhoneNumberABVariant" });

                    intervalClosure = setInterval(() => {
                        if (window.google_optimize !== undefined) {
                            const variant = window.google_optimize.get(optimizePhoneABKey);
                            if (variant) {
                                setVariant(variant);
                                window.localStorage.setItem(localStorageKey, variant);
                                clearInterval(intervalClosure);
                                // eslint-disable-next-line no-plusplus
                            } else if (++waitCounter > 20) {
                                setVariant(originVariant);
                                clearInterval(intervalClosure);
                            }
                        }
                    }, 100);
                }
            };

            setOptimize();

            return () => {
                clearInterval(intervalClosure);
            };
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return null;
};

export default CheckPhoneNumberABVariant;
