import { reaction } from "mobx";
import { breakPoints } from "#screens/index";

function preloadPortfolio(rootStore) {
    const { tablet } = breakPoints;
    const isTablet = window.innerWidth <= tablet;

    const initialPortfolioLoad = reaction(
        () =>
            rootStore.user?.profileId &&
            rootStore.auth.isAuthenticated &&
            !rootStore.portfolio?.winesList?.wines?.length &&
            !rootStore.portfolio?.winesList?.nextPageToken,
        (ok?: any, prevVal?: any, react?: any) => {
            if (ok) {
                rootStore.portfolio.fetchPortfolioWines();
                rootStore.portfolio.fetchPortfolioTotals();
            }
        },
    );
    const subsequentPortfolioLoad =
        !isTablet &&
        reaction(
            () =>
                rootStore.user?.profileId &&
                rootStore.auth.isAuthenticated &&
                rootStore.portfolio?.winesList?.wines?.length &&
                rootStore.portfolio?.winesList?.nextPageToken,
            async (nextPageToken?: any, previousValue?: any, react?: any) => {
                if (nextPageToken) {
                    rootStore.portfolio.fetchPortfolioWines();
                }
            },
        );

    return {
        initialPortfolioLoad,
        subsequentPortfolioLoad,
    };
}

export default function (rootStore) {
    return Promise.all([preloadPortfolio(rootStore)]);
}
