import React, { useState, useEffect, useCallback } from "react";
import { observer } from "mobx-react-lite";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import posthog from "posthog-js";
import { useMobile, useConfig, useRootStore } from "#shared/hooks";
import { ReactComponent as RightArrowIcon } from "#assets/shared/right_arrow.svg";

export const CalendlyBanner = observer(({ offset }) => {
    const store = useRootStore();
    const config = useConfig();
    const isMobile = useMobile();
    const [shouldExpand, setShouldExpand] = useState(false);
    const { t } = useTranslation("common");

    const onScheduleClick = useCallback(() => {
        posthog.capture("calendly", { component: "CalendlyBanner", progress: "launch" });
        store.tracking.gtm.trackBannerAction("Click Banner");
        window.Calendly.showPopupWidget(config.calendly.url);
    }, [config, store.tracking.gtm]);

    useEffect(() => {
        setTimeout(() => {
            store.tracking.gtm.trackBannerAction("View Banner");
            setShouldExpand(true);
        }, 1500);
    }, []);

    return (
        <BannerButton role="button" onClick={onScheduleClick} shouldExpand={shouldExpand} offset={offset}>
            <BannerCopy>{isMobile ? t("calendly.mobile-book") : t("calendly.book")} </BannerCopy>
            <RightArrowIcon />
        </BannerButton>
    );
});

const BannerButton = styled.button`
    margin-top: -${({ offset }) => offset}px;
    transition: height 0.3s ease-out, margin 0.3s;
    padding: 0;
    height: ${(p) => (p.shouldExpand ? "60px" : "0px")};
    opacity: ${(p) => (p.shouldExpand ? "1" : "0")};
    color: ${({ theme }) => theme.colors.mainInnerTaupe};
    background: ${({ theme }) => theme.colors.mainAccentBlue};
    width: 100%;
    border: none;
    outline: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const BannerCopy = styled.div`
    margin-right: 20px;
`;
