import React, { memo } from "react";
import styled from "styled-components";
import PropTypes from "prop-types";
import PulseLoader from "react-spinners/PulseLoader";
import { MainButton } from "../ui";

export const MainButtonWithLoader = memo(({ isLoading, disabled, onClick, CTA }) => (
    <MainButtonWithMargins disabled={isLoading || disabled} onClick={onClick}>
        {isLoading ? <PulseLoader size={8} margin="10px" color="#828282" loading={isLoading} /> : CTA}
    </MainButtonWithMargins>
));

// clone of DepositMainButton, until you can sever this tie
const MainButtonWithMargins = styled(MainButton)`
    margin-top: 32px;
    @media screen and (min-width: 768px) {
        margin-top: 40px;
        max-width: initial;
    }
`;

MainButtonWithLoader.defaultProps = {
    isLoading: false,
    disabled: false,
};
MainButtonWithLoader.propTypes = {
    isLoading: PropTypes.bool,
    disabled: PropTypes.bool,
    onClick: PropTypes.func.isRequired,
    CTA: PropTypes.string.isRequired,
};
