import { useCallback } from "react";

function withinInverval(currentTime, startTime, endTime) {
    const currentHours = currentTime.getUTCHours();
    const startHours = startTime.getUTCHours();
    const endHours = endTime.getUTCHours();
    const inInterval = currentHours >= startHours || currentHours <= endHours;
    return inInterval;
}

interface ITimedCallbackOptions {
    startTime: Date;
    endTime: Date;
    timeDisabled?: boolean;
}
export const useTimedCallback = (
    cb: VoidFunction,
    deps: any,
    { startTime, endTime, timeDisabled }: ITimedCallbackOptions,
) => {
    const currentTime = new Date();

    const timedCallback = useCallback(() => {
        if (withinInverval(currentTime, startTime, endTime) || timeDisabled) {
            cb();
        }
    }, [...deps, cb, startTime, endTime, currentTime]);

    return timedCallback;
};
