import React from "react";
import { useLocation } from "react-router-dom";
// import { useTranslation } from "react-i18next";
import { useRootStore } from "#shared/hooks";
import { checkHasToken } from "#utils/shared";
import Navigation from "./navigation/index";
import { FooterContainer, GridContainer } from "./styles";
import RecentArticles from "./recent";
import Newsletters from "./Newsletters";
import Support from "./support";
import WineInvestingBanner from "./StartInvestingBanner";

const Footer = ({ embed }) => {
    // const { t } = useTranslation("common");
    const store = useRootStore();
    const { pathname } = useLocation();
    const authenticated = store.auth.isAuthenticated || checkHasToken();

    const hideFooter =
        ["/login", "/reset_password", "/signup"].includes(window.location.pathname) ||
        window.location.pathname.includes("/deposit");

    const shouldRender = !pathname.includes("personalize") && !hideFooter;

    if (!shouldRender || window.location.pathname.includes("/implicit")) {
        return null;
    }

    return (
        <div>
            {!authenticated && <WineInvestingBanner />}
            <FooterContainer embed={embed} withbanner={authenticated ? 0 : 1}>
                <GridContainer>
                    <div className="a" withbanner={authenticated ? 0 : 1}>
                        <Navigation />
                    </div>
                    <div className="b">
                        <RecentArticles />
                    </div>
                    <div className="c">
                        <Newsletters />
                    </div>
                    <div className="d">
                        <Support />
                    </div>
                </GridContainer>
            </FooterContainer>
        </div>
    );
};

export default Footer;
