/* eslint-disable import/no-unresolved */
/* eslint-disable import/extensions */
import React from "react";
import styled from "styled-components";
import {useTranslation} from "react-i18next";
import MenuBtnSvg from "#assets/navigation/MenuBtn.js";
import { V3Logo } from "#assets/shared/v3Logo";
import { DepositButton } from "#shared/components/Deposit";
import { NavLogo, StyledNav, FirstNavSection, NavAnimationWrapper, NavAnimationContainer } from "../Navigation.styled";
import referralBadge from "#assets/navigation/referral-badge.svg";
import { ConditionalLink } from "./ConditionaLink";

const MobileNavBar = ({
    heightMobile,
    routetoInvite,
    toggleMobileNavMenu,
    classNameHeader,
    isOnboarding,
    logoType,
    isSticky,
    pathname,
    isAddFunds,
    authenticated,
    routeToDeposit,
    routeToSignup,
}) => (
    <NavAnimationWrapper className={isSticky || isAddFunds ? "sticky" : ""}>
        <NavAnimationContainer className={isSticky || isAddFunds ? "openNavBar" : "closeBavBar"}>
            <StyledNav isSticky height={heightMobile} className="darkBlue darkBlue__constant sticky">
                <FirstNavMobile
                    {...{
                        routetoInvite,
                        toggleMobileNavMenu,
                        classNameHeader: "light",
                        isOnboarding,
                        logoType,
                        isSticky: true,
                        pathname,
                        isAddFunds,
                        authenticated,
                        routeToDeposit,
                        routeToSignup,
                    }}
                />
            </StyledNav>
        </NavAnimationContainer>

        {!isAddFunds && (
            <NavAnimationContainer className={`outFromHtml ${!isSticky ? "openNavBar" : "closeBavBar"}`}>
                <StyledNav isSticky={false} height={heightMobile}>
                    <FirstNavMobile
                        {...{
                            routetoInvite,
                            toggleMobileNavMenu,
                            classNameHeader,
                            isOnboarding,
                            logoType,
                            isSticky: false,
                            pathname,
                            isAddFunds,
                            authenticated,
                            routeToDeposit,
                            routeToSignup,
                        }}
                    />
                </StyledNav>
            </NavAnimationContainer>
        )}
    </NavAnimationWrapper>
);

const FirstNavMobile = ({
    routetoInvite,
    toggleMobileNavMenu,
    classNameHeader,
    isOnboarding,
    logoType,
    isSticky,
    pathname,
    isAddFunds,
    authenticated,
    routeToDeposit,
    routeToSignup,
}) => {
    const {t} = useTranslation('common');
    return (
    <FirstNavSection className={isSticky ? "sticky" : ""}>
        {!pathname.includes("personalize") && !isAddFunds && (
            <>
                {authenticated ? (
                    <ReferralBadgeLink onClick={routetoInvite}>
                        <img src={referralBadge} alt="referral-badge" />
                    </ReferralBadgeLink>
                ) : (
                    <OpenMenuBtn onClick={toggleMobileNavMenu}>
                        <MenuBtnSvg colorClassName={classNameHeader || "light"} />
                    </OpenMenuBtn>
                )}
            </>
        )}

        <NavLogo isCompact={isSticky}>
            <ConditionalLink condition={!isOnboarding}>
                <V3Logo isCompact={isSticky} type={isSticky ? "standard" : logoType} />
            </ConditionalLink>
        </NavLogo>

        {isSticky && !pathname.includes("personalize") && !isAddFunds && !authenticated && (
            <ButtonWrap>
                <DepositButton id="nav-deposit-button" onClick={routeToSignup}>
                    {t('get-started')}
                </DepositButton>
            </ButtonWrap>
        )}
        {!pathname.includes("personalize") && !isAddFunds && authenticated && (
            <ButtonWrap>
                <DepositButton id="nav-deposit-button" onClick={routeToDeposit}>
                    {t("navigation.funds")}
                </DepositButton>
            </ButtonWrap>
        )}
    </FirstNavSection>
)};

const OpenMenuBtn = styled.div`
    height: fit-content;
    &:hover {
        cursor: pointer;
    }
`;

const ButtonWrap = styled.div`
    height: fit-content;
    #nav-deposit-button {
        height: 37px;
        line-height: initial;
        font-size: 12px;
        padding: 7px 10px 6px;
        @media screen and (max-width: 340px) {
            white-space: nowrap;
            position: relative;
            right: -9px;
        }
    }
`;

const ReferralBadgeLink = styled.a`
    height: fit-content;
    cursor: pointer;
`;

export default MobileNavBar;
