import styled from "styled-components";
import { I18nNavLink } from "#localization/localizedRouter.tsx";

const activeClassName = "active";

const styles = `
    font-family: "VinovestMono";
    text-transform: uppercase;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    margin: 0 40px 0 0;
    text-align: center;
    color: inherit;
    cursor: pointer;
    text-decoration: none;
    position: relative;
`;

export const StyledNavLink = styled(I18nNavLink).attrs({ activeClassName })`
    ${styles};

    &.${activeClassName} {
        &:after {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            content: "";
            display: block;
            margin: 0 auto;
            width: 26px;
            border-bottom: 2px solid ${({ theme }) => theme.colors.mainAccentBlue};
            padding-bottom: 5px;
        }
        &.transparent {
            &:after {
                border-bottom: 2px solid #efddc7;
            }
        }

        &.light {
            &:after {
                border-bottom: 2px solid #242e35;
            }
        }

        &.green {
            &:after {
                border-bottom: 2px solid #513011;
            }
        }

        &.blue {
            &:after {
                border-bottom: 2px solid #3c400c;
            }
        }

        &.darkBlue {
            &:after {
                border-bottom: 2px solid #EFDDC7;
            }
        }
    }
`;

export const LinkHtml = styled.a`
    ${styles};
`;
