import React, { useEffect, useMemo, Suspense } from "react";
import cookie from "js-cookie";
import { Security, useOktaAuth } from "@okta/okta-react";
import _pick from "lodash/pick";
import { BrowserRouter as Router } from "react-router-dom";
import posthog from "posthog-js";
import ServiceBell from "@servicebell/widget";

import { ToastContainer } from "react-toastify";
import { useLocation } from "react-router";
import { observer } from "mobx-react-lite";
import { withProfiler as withSentryProfiler, setUser as setSentryUser } from "@sentry/react";
import { ThemeProvider } from "styled-components";
import { I18nSwitch, I18nRoute } from "#localization/localizedRouter";
import Footer from "#shared/components/Footer";
import { AppHeader } from "#shared/components/Navigation/AppHeader";
import { MobileNavBar } from "#shared/components/Navigation/MobileNavBar";
import { useRootStore, useMobile, useCreateRoutingCallback } from "#shared/hooks";
import RouteFactory from "#shared/components/RouteFactory";
import { theme } from "#assets/theme/theme";
import { getStatus } from "#models/FetchStatus";
import { Routes } from "#screens/index";
import { PendingReferralInviteModal } from "#shared/components/PendingReferralInviteModal";
import { InviteModal } from "#screens/invite/InviteModal";
import ScreenSpinner from "#shared/components/ScreenSpinner";
import { EmailCaptureCookie, ReferralInviteCookie, AUTH_TYPE, REDIRECT_URI } from "#utils/constants";
import ScrollToTop from "#shared/components/ScrollToTop";
import RemoveOptinmonster from "#shared/components/RemoveOptinmonster";
import SignUpModal from "#shared/components/SignUpModal";
import CheckPhoneNumberABVariant from "#shared/components/CheckPhoneNumberABVariant";

const onAuthRequired = () => {
    /* TODO: replace this magical implementation with the "redirect_uri" query param pattern */
    localStorage.setItem(REDIRECT_URI.KEY, JSON.stringify(_pick(window.location, ["pathname", "search"])));

    /* reads emailCapture cookie to auto-fill register form */
    const emailCaptureCookie = cookie.get(EmailCaptureCookie.KEY);
    const authDestination = emailCaptureCookie ? "signup" : "login";

    window.location.href = `/${authDestination}`; // forces in-memory state to reset
};

const Body: React.VFC = observer(() => {
    const s = useRootStore();
    const location = useLocation();
    const { oktaAuth, authState } = useOktaAuth();

    const isMobile = useMobile(1023);
    const routeTo = useCreateRoutingCallback();

    /**      MEMOIZED FLAGS     */
    const hasUserDataLoaded = useMemo(
        () => getStatus(s.user.oktaUserEntity).isDone() && getStatus(s.user.profileEntity).isDone(),
        [s.user.oktaUserEntity, s.user.profileEntity],
    );

    const shouldShowCalendlyBanner = useMemo(() => {
        if ((s.portfolio.accountLevel === "Standard" && s.portfolio.totalAccountValue > 0) || !s.auth.isAuthenticated) {
            return false;
        }
        return ["/", "/portfolio", "/invite"].includes(location.pathname);
    }, [location, s.portfolio.totalAccountValue, s.portfolio.accountLevel, s.auth.isAuthenticated]);

    const shouldShowReferralInviteModal = useMemo(
        () =>
            s.referral.hasPendingReferralInvite && ["/"].includes(location.pathname) && !s.transfer.transferInProgress,
        [location, s.referral.hasPendingReferralInvite, s.transfer.transferInProgress],
    );
    /** Auth: set jwt to store if authenticated */
    useEffect(() => {
        if (!s.auth.accessToken) {
            s.auth.checkAndSetAJwt(authState);
        }
    }, [authState, s.auth, s.auth.isAuthenticated]);

    /** Referral: set cookie on app load if query parameter exists */
    useEffect(() => {
        const query = new URLSearchParams(location.search);
        const referralInviteCode = query.get("grsf");
        if (referralInviteCode) {
            cookie.set(ReferralInviteCookie.KEY, referralInviteCode, ReferralInviteCookie.CONFIG);
        }
    }, [location]);

    /** Fetch: userinfo, profile, referenceKey, portfolio, fees, referral, transfers */
    useEffect(() => {
        const fetchUserProfileData = async () => {
            await s.user.requestUserDetailsFromOkta(oktaAuth);

            // don't block on these -- has to call out to Stripe
            s.transfer.fetchBidsActive();
            s.transfer.fetchShouldShowAccountSubscriptionEntity();

            await Promise.all([
                s.user.fetchProfile(),
                s.deposit.fetchReferenceKey(),
                s.portfolio.fetchPortfolioTotals(),
            ]);

            await s.referral.pollForUserReferralDetails(4);
            await s.referral.validateReferralInviteIfExists();
        };

        if (s.auth.isAuthenticated) {
            fetchUserProfileData();
        }
    }, [authState, oktaAuth, s, s.auth.isAuthenticated]);

    /** Events: login or signup */
    useEffect(() => {
        if (getStatus(s.user.oktaUserEntity).isDone()) {
            const authType = localStorage.getItem(AUTH_TYPE.KEY);
            if (authType === AUTH_TYPE.SIGNUP) {
                s.tracking.gtm.trackRegistrationComplete();
                s.tracking.gtm.trackCreatePassword(s);
            }

            if (authType === AUTH_TYPE.LOGIN) {
                s.tracking.gtm.trackLoginComplete();
            }
            const { status, ...userInfo } = s.user.oktaUserEntity;
            const { sub, ...restUserInfo } = s.user.oktaUserInfo;
            posthog.identify(sub, { ...restUserInfo });
            setSentryUser(userInfo);
            ServiceBell.identify(sub, {
                displayName: restUserInfo.name,
                ...restUserInfo,
            });
            localStorage.removeItem(AUTH_TYPE.KEY);
        }
    }, [s.user.oktaUserEntity, s.tracking.gtm, s]);

    /** Redirect: to Quiz if user needs onboarding */
    useEffect(() => {
        if (hasUserDataLoaded && s.user.needsOnboarding && s.user.phoneNumberABVariant && !location.pathname.includes("personalize")) {
            s.user.phoneNumberABVariant === "0" ? routeTo("/personalize/start") : routeTo("/personalize/enter-number");
        }
    }, [hasUserDataLoaded, s.user.needsOnboarding, routeTo, s.user.phoneNumberABVariant, location.pathname]);

    const fullScreenMode = !!["/print"].filter((e) => location.pathname.includes(e)).length;

    const showCheckPhoneNumber =
        (s.user.needsOnboarding || location.pathname.includes("personalize")) &&
        s.user.oktaUserEntity.data &&
        s.user.oktaUserEntity.data.email;

    return (
        <div id="page-wrap">
            {showCheckPhoneNumber && (
                <CheckPhoneNumberABVariant
                    setVariant={s.user.setPhoneNumberABVariant}
                    oktaUserEntity={s.user.oktaUserEntity}
                />
            )}

            {!fullScreenMode && <AppHeader shouldShowCalendlyBanner={shouldShowCalendlyBanner} />}
            <SignUpModal />
            <main>
                <Suspense fallback={<ScreenSpinner loading />}>
                    <I18nSwitch>
                        {Routes.map((screen, i) => (
                            <RouteFactory {...screen} key={screen.path} />
                        ))}
                    </I18nSwitch>
                </Suspense>

                <PendingReferralInviteModal showPendingInviteModal={shouldShowReferralInviteModal} />
            </main>
            {!fullScreenMode && (
                <>
                    <Footer />
                    {isMobile && <MobileNavBar shouldShowCalendlyBanner={shouldShowCalendlyBanner} />}
                    {s.referral.referralInviteModalWindowOpen && (
                        <InviteModal onClose={s.referral.toggleReferralInviteModalWindowOpen} />
                    )}
                </>
            )}
        </div>
    );
});

const App = ({ oktaAuthInstance }) => (
    <ThemeProvider theme={theme}>
        <Router>
            <ScrollToTop />
            <RemoveOptinmonster />
            {/* https://developer.okta.com/quickstart/#update-your-app-js */}
            <Security onAuthRequired={onAuthRequired} oktaAuth={oktaAuthInstance}>
                <div id="outer-container">
                    <I18nRoute>
                        <Body />
                    </I18nRoute>
                    <ToastContainer autoClose={5000} hideProgressBar closeOnClick draggable />
                </div>
            </Security>
        </Router>
    </ThemeProvider>
);

export default withSentryProfiler(App);
