import { useCallback } from "react";
import { useLocation, useHistory } from "react-router-dom";
import posthog from "posthog-js";
import { localizedPath } from "#localization/localizedRouter";

export const useCreateRoutingCallback = (path, posthogID) => {
    const history = useHistory();
    const location = useLocation();
    return useCallback(
        (deferredPath) => {
            const localePath = {
                pathname: localizedPath(path) || localizedPath(deferredPath),
                search: location.search,
            };
            history.push(localePath);
            posthog.capture(posthogID, {});
        },
        [path, location.search, history, posthogID],
    );
};
