import * as Sentry from "@sentry/react";
import { Integrations } from "@sentry/tracing";
import { matchPath } from "react-router-dom";
import { Routes as routes } from "../screens/index";

export class SentryClient {
    static build(config, history) {
        const client = new SentryClient(config, history);
        return client;
    }

    constructor(config, history) {
        const { dsn, release, tracesSampleRate = 0.05, environment = "production", tracingOrigins } = config.sentry;

        Sentry.init({
            dsn,
            release,
            tracesSampleRate,
            environment: config.env,
            integrations: [
                new Integrations.BrowserTracing({
                    routingInstrumentation: Sentry.reactRouterV4Instrumentation(history, routes, matchPath),
                    tracingOrigins,
                }),
            ],
        });
    }
}
