/* eslint-disable prefer-destructuring */
import React, { useMemo, useCallback, useEffect, useState } from "react";
import { usePlaidLink } from "react-plaid-link";
import PulseLoader from "react-spinners/PulseLoader";
import _ from "lodash";
import { observer } from "mobx-react-lite";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import posthog from "posthog-js";
import { I18nLink } from "#localization/localizedRouter";
import { useHistory } from "#shared/hooks/useHistory";
import { ROUTE_PATHS } from "#screens/route-paths";

import WireBankSVG from "#assets/shared/wire-bank.svg";
import CreditCardSVG from "#assets/shared/credit-card.svg";
import { useRootStore, useConfig, useCreateRoutingCallback } from "#shared/hooks";
import { BackButtonFixed, Fade } from "#shared/ui";
import BitpayPaymentSourceOption from "./PaymentSources/BitpayPaymentSource";
import { DepositPageFrameContainer, AlertMessage, OptionListContainer, PaymentSourceOptionContainer } from "./styles";
import BankAccountOption from "./PaymentSources/BankAccount";
import PaymentSourceOption from "./PaymentSources/PaymentSourceOption";
import { IAppConfig } from "#app.config";
import { DepositEvent } from "./RootDepositPage";

export interface ISelectSourceAlert {
    message: string;
    level: "log" | "warn" | "error";
}
export const SelectSource = observer(() => {
    const { t } = useTranslation(["deposit"]);

    const { deposit: depositStore, user } = useRootStore();
    const defaultAlert: ISelectSourceAlert = { message: "", level: "warn" };
    const [alert, setAlert] = useState(defaultAlert);
    const config: IAppConfig = useConfig();
    const history = useHistory();
    const routeTo = useCreateRoutingCallback();

    useEffect(() => {
        if (depositStore.isSubscription) {
            setAlert((a) => ({
                ...a,
                message: t("alert_message"),
            }));
        }
    }, [depositStore.isSubscription, t]);

    const onSelectCreditCardSource = useCallback(() => {
        routeTo("/deposit/credit-card");
        posthog.capture(DepositEvent.SelectCreditCard);
    }, [routeTo]);

    const onSelectWireTransfer = useCallback(() => {
        routeTo("/deposit/wire-transfer-type");
        posthog.capture(DepositEvent.SelectWire);
    }, [routeTo]);

    const isCreateACHAccountPending: boolean = useMemo(() => depositStore.ACHAccountCreate.status.isPending(), [
        depositStore.ACHAccountCreate.status,
    ]);

    return (
        <Fade in>
            <DepositPageFrameContainer>
                {alert.message && (
                    <Fade in>
                        <AlertMessage role="alert" level={alert.level}>
                            {alert.message}
                        </AlertMessage>
                    </Fade>
                )}

                <OptionListContainer>
                    <BankAccountOption plaidToken={depositStore.plaidLinkToken} setAlert={setAlert} />
                    <PaymentSourceOption
                        onClick={onSelectCreditCardSource}
                        disabled={isCreateACHAccountPending}
                        iconSrc={CreditCardSVG}
                        label={t("credit_card")}
                    />
                    <BitpayPaymentSourceOption
                        isCreateACHAccountPending={isCreateACHAccountPending}
                        setAlert={setAlert}
                    />
                    <PaymentSourceOption
                        onClick={onSelectWireTransfer}
                        disabled={depositStore.isSubscription || isCreateACHAccountPending}
                        iconSrc={WireBankSVG}
                        label={t("bank_wire")}
                    />
                    {isCreateACHAccountPending && (
                        <LoadingShield>
                            <PulseLoader color="#828282" />
                        </LoadingShield>
                    )}
                </OptionListContainer>
                <SeeInstructions>
                    {`${t("mail_check_deposit")} `}
                    <I18nLink to={`${ROUTE_PATHS.deposit}/mail-a-check`} target="_blank">
                        {t("instructions")}
                    </I18nLink>
                </SeeInstructions>

                <BackButtonFixed goBack={history.goBack} />
            </DepositPageFrameContainer>
        </Fade>
    );
});

const LoadingShield = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: not-allowed;
`;

const SeeInstructions = styled.div`
    font-family: VinovestMedium;
    font-style: normal;
    font-weight: normal;
    margin-top: 27px;
    font-size: 11px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.025em;
    text-transform: uppercase;
    color: #242e35;

    a {
        color: #a86d37;
        text-decoration: none;
    }

    ${(p) => p.theme.media.greaterThan("768px")`
        margin-top: 41px;
        font-size: 14px;
        line-height: 18px;
    `};
`;
