import React from "react";
import { observer } from "mobx-react-lite";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

import { ModalBase } from "#shared/components/ModalBase";
import { Fade } from "#shared/ui";
import { EmailCaptureCookie } from "#utils/constants";
import { ROUTE_PATHS } from "#screens/route-paths";

import internationalSvg from "../../../screens/login/assets/international.svg";
import { InternationalFlags } from "../../../screens/login/components/styled";
import { useRootStore, useCreateRoutingCallback } from "../../hooks";
import {
    ContentWraper,
    ContinueBtn,
    BaseStyles,
    Title,
    EmailInput,
    Disclaimer,
    ModalContainer,
    InternationalClient,
} from "./styles";

const SignUpModel = observer(() => {
    const { t } = useTranslation(["sign-up-modal"]);
    const s = useRootStore();
    const [email, setEmail] = React.useState("");
    const redirectToSignUpCB = useCreateRoutingCallback("/signup");

    const onSubmit = (e) => {
        e.preventDefault();
        document.cookie = `${EmailCaptureCookie.KEY}=${email};domain=${EmailCaptureCookie.CONFIG.domain};path=${EmailCaptureCookie.CONFIG.path};`;
        setEmail("");
        redirectToSignUpCB();
        SignupModalFalse();
    };

    const SignupModalFalse = () => {
        s.user.signUpModalToggle(false);
    };

    return s.user.signUpModalWindow ? (
        <ModalBase
            isOpen
            additionalStyles={BaseStyles}
            onClose={() => {
                s.user.signUpModalToggle();
            }}
        >
            <Fade in>
                <ModalContainer>
                    <ContentWraper>
                        <Title>{t("title")}</Title>
                        <form onSubmit={onSubmit}>
                            <EmailInput
                                required
                                type="email"
                                placeholder={t("email_placeholder")}
                                value={email}
                                onChange={(e) => {
                                    setEmail(e.target.value);
                                }}
                            />
                            <ContinueBtn>{t("email_placeholder")}</ContinueBtn>
                        </form>
                        <Disclaimer>
                            {t("disclaimer")}{" "}
                            <Link onClick={SignupModalFalse} to={ROUTE_PATHS.privacyPolicy}>
                                {t("privacy")}
                            </Link>{" "}
                            {t("end")}{" "}
                            <Link onClick={SignupModalFalse} to={ROUTE_PATHS.terms}>
                                {t("terms")}
                            </Link>{" "}
                            .
                        </Disclaimer>
                        <InternationalClient>{t("available_to_clients")}</InternationalClient>
                        <InternationalFlags src={internationalSvg} alt="flags" />
                    </ContentWraper>
                </ModalContainer>
            </Fade>
        </ModalBase>
    ) : null;
});

export default SignUpModel;
